.charts-empty {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  .empty-inner {
    width: 220px;
    height: 225px;
    .empty-img {
      width: 220px;
      height: 183px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .empty-text {
      margin-top: 20px;
      font-size: 15px;
      font-weight: 600;
      text-align: center;
      color: #ffffff;
      line-height: 21px;
      opacity: 0.3;
    }
  }
}
