@import './var.scss';
@import './theme';
$blue: #5915e1;
$hoverBlue: #3940b7;
$purple: rgba(72, 80, 229, 0.2);
$hoverPurple: #bdc0f5;
$bgColor: #140736;

#root .base-scan-table {
  overflow: hidden;
  background: $color-table-bg;
  .tokenSymbol {
    img {
      width: 16px;
      vertical-align: middle;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }

    .pairIndex {
      margin-right: 10px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 21px;
      color: $color-text-default-3;
      width: 20px;
    }

    .tokens {
      position: relative;
      top: -1px;

      &.color-border {
        span {
          border-radius: 100%;
          width: 22px;
          height: 22px;
          display: inline-block;

          img {
            border-radius: 100%;
          }
        }
      }

      .tokens-logo2 {
        margin-left: -5px;
      }
    }

    .curve-tokens {
      img {
        // display: block;
        width: 37px;
        height: 20px;
        border: none;
        border-radius: 0;
        background: transparent;
      }
    }
    .curve-tokens-home {
      img {
        display: block;
        width: 26px;
        height: 14px;
      }
    }

    .token-names {
      margin-left: 10px;
      flex: 1;
      vertical-align: middle;
      color: $color-text-table-token;
      &.home-token-names {
        color: $color-text-default;
      }
    }
  }
