  .btn-list {
    display: flex;
    .btn-link-item {
      margin-right: 10px;
      padding: 4px 13px;
      min-width: 110px;
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #5a14e1;
      line-height: 21px;
      border-radius: 4px;
      white-space: nowrap;
      background: #decdff;
      text-align: center;
      cursor: pointer;
      &:hover {
        background: #bdc0f5;
      }
      &:last-child {
        margin-right: 0;
