@import './var.scss';
@import './mixins';

.header-tvl {
  .tvl-box {
    display: flex;
    flex-wrap: wrap;
    .tlv-item {
      margin-right: 60px;
      white-space: nowrap;

      &:last-child {
        margin-right: 0;
      }
      .text {
        position: relative;
        top: -4px;
        margin-right: 10px;
        font-size: 15px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        line-height: 21px;
        color: $color-text-default-5;
      }
      .num {
        font-size: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-text-default;
        line-height: 42px;
      }
    }
  }
}

@include desktop-only {
  .mobile {
    display: none !important;
  }
}

@include mobile-only {
  .pc {
    display: none !important;
  }
  .header-tvl {
    padding: 22px 12px;
    background: $color-normal-card-bg;
    border-radius: 10px;
    @include lightThemeOnly {
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    }
    .tvl-box {
      &.mobile {
        display: flex !important;
        flex-wrap: wrap;
      }

      .tlv-item {
        display: flex;
        margin-right: 0;
        white-space: normal;
        &:nth-child(1) {
          margin-bottom: 30px;
        }
        &:nth-child(2) {
          margin-bottom: 30px;
        }
        width: 50%;
        .icon {
          padding-top: 2px;
          margin-right: 10px;
          img {
            display: block;
            width: 38px;
            height: 38px;
          }
        }
        .info {
          flex: 1;
          width: 0;
          span {
            display: block;
          }
          .text {
            margin-top: 5px;
            font-size: 12px;
            font-weight: 400;
            color: $color-text-default-5;
            line-height: 17px;
          }
          .num {
            // margin-top: 4px;
            font-size: 16px;
            font-weight: 600;
            color: $color-text-default;
            line-height: 22px;
          }
        }
      }
    }
  }
}
