@import './var.scss';
@import './theme';
@import './mixins';

.new-scan {
  .ant-tabs-top > .ant-tabs-nav::before {
    display: none;
  }
  .new-scan-main {
    margin: 0 auto;
    width: 100%;
    max-width: min(1200px, 90vw);

    .new-scan-section {
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .new-scan-title {
      margin-bottom: 20px;
      padding: 0 10px;
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 28px;
    }
    .table-box {
      border-radius: 20px;
      overflow: hidden;
      background: $color-normal-card-bg;
      @include lightThemeOnly {
        box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
      }

      .ant-tabs-nav {
        margin: 0 0 10px 0;
      }
      .ant-tabs-nav-wrap {
        padding: 10px 40px;
        .ant-tabs-ink-bar {
          height: 4px;
          background: transparent;
          &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 0;
            width: 30px;
            height: 100%;
            background: linear-gradient(90deg, #9482f8 0%, #e64dff 100%);
            border-radius: 2px;
            transform: translateX(-50%);
          }
        }
        .ant-tabs-tab {
          padding: 10px 0;
          font-size: 20px;
          line-height: 28px;
          color: $color-text-default;
          opacity: 0.3;
          &.ant-tabs-tab-active {
            opacity: 1;
            .ant-tabs-tab-btn {
              color: $color-text-default;
            }
          }
          & + .ant-tabs-tab {
            margin: 0 0 0 30px;
          }
          .ant-tabs-tab-btn {
            &:active {
              color: $color-text-default;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  .token-wrap {
    padding: 20px 40px 30px;
    display: flex;
    color: $color-text-default;
    .token-select-label {
      font-size: 20px;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.3);
      margin-right: 10px;
      height: 40px;
      line-height: 40px;
      &-split {
        margin-left: 30px;
      }
    }
    .token-select {
      display: flex;
      align-items: center;
      // justify-content: center;
      min-width: 160px;
      width: auto;
      margin-right: 10px;
      padding-right: 10px;
      height: 40px;
      background: #444454;
      border-radius: 10px;
      cursor: pointer;
      user-select: none;
      &:last-child {
        margin-right: 0;
      }
      .selector {
        display: inline-block;
        width: 100%;
        white-space: nowrap;
        padding-left: 16px;
        padding-right: 22px;
        background: url(../images/white-triangle-down.svg) 100% no-repeat;
        background-position: 100% 50%;
        img {
          width: 24px;
          height: 24px;
          vertical-align: middle;
        }
        span {
          position: relative;
          top: 1px;
          line-height: 24px;
          font-family: AvenirNext-Medium;
          font-size: 20px;
          color: $color-text-default;
          vertical-align: middle;
          display: inline-block;
          margin: 0 0 0 6px;
        }
      }
    }
  }

  .line-type-area {
    .header {
      display: flex;
      align-items: center;
      padding: 0 40px 20px;
      .total {
        margin-right: 10px;
        font-size: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-text-default;
        line-height: 42px;
      }
      .rate {
        font-size: 15px;
        font-family: PingFangSC-Semibold, PingFang SC;
        line-height: 21px;
      }
      .header-l {
        flex: 1;
      }
    }
  }

  .price-card {
    .header-wrap {
      padding: 0 40px 20px;
    }
    .header-top {
      display: flex;
      align-items: center;
      .header-l {
        flex: 1;
      }
    }
    .time {
      margin: 0 10px;
      font-size: 12px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.3);
    }
    .total {
      margin-right: 10px;
      font-size: 30px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 42px;
    }
    .rate {
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      line-height: 21px;
    }
    .header-row {
      margin-top: 10px;
      span {
        margin-right: 3px;
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default-3;
        line-height: 17px;
        &:nth-child(2n) {
          margin-right: 10px;
        }
        &:last-child {
          margin-right: 0;
        }
      }
      .num {
        color: $color-text-default;
      }
      .color-white {
        .num {
          color: $color-text-default;
        }
      }
      .color-red2 {
        .num {
          color: #ff4f88;
        }
      }
      .color-green {
        .num {
          color: #16c378;
        }
      }
    }
  }

  .time-picker {
    display: flex;
    background: rgba(193, 160, 255, 0.5);
    color: $color-text-default-5;
    border-radius: 30px;
    overflow: hidden;
    .time-picker-item {
      min-width: 70px;
      padding: 2px 15px;
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      line-height: 17px;
      text-align: center;
      user-select: none;
      border-radius: 30px;
      white-space: nowrap;
      cursor: pointer;
    }
    .time-picker-active {
      background: rgba(193, 160, 255, 1);
      color: rgba(90, 20, 225, 1);
    }
  }

  .color-red2 {
    color: #ff4f88;
  }

  .color-green {
    color: #16c378;
  }

  .color-white {
    color: $color-text-default !important;
  }

  .index-tvl-wrap {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .index-search-wrap {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .index-select-wrap {
    margin-bottom: 40px;
  }
}

