@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.scan_trade_card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  text-align: left;
  font-style: normal;
  margin-bottom: 20px;

  @include mobile-only {
    margin-top: 10px;
    margin-bottom: 20px;
  }

  &_item {
    width: calc((100% - 60px) / 4);
    height: 110px;
    background: #403858;
    @include lightThemeOnly {
      background: #fff;
    }
    border-radius: 10px;
    flex-shrink: 0;
    margin-bottom: 15px;
    padding: 16px 20px;
  }
  &_key {
    font-weight: 400;
    font-size: 14px;
    color: $color-text-default-7;
    line-height: 20px;
    text-align: left;
    margin-bottom: 12px;
  }
  &_value {
    font-weight: 500;
    font-size: 30px;
    color: $color-text-default;
    line-height: 42px;
    font-style: normal;
  }
  &_item_m {
    width: calc((100% - 5px) / 2);
    height: 77px;
    background: #403858;
    margin-bottom: 5px;
    padding: 12px;
    border-radius: 5px;
    //&:first-child {
    //  width: 100%;
    //  display: flex;
    //  padding: 10px;
    //  height: auto;
    //  align-items: center;
    //  .scan_trade_card_key {
    //    margin-bottom: 0;
    //    margin-right: 10px;
    //  }
    //}

    @include lightThemeOnly {
      background: #fff;
    }
    .scan_trade_card_key {
      font-size: 12px;
      line-height: 17px;
      margin-bottom: 10px;
    }
    .scan_trade_card_value {
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
    }
  }
}
