@import './var.scss';
@import './theme';
@import './mixins';
.version-select {
  display: flex;
  align-items: center;
  .label {
    margin-right: 20px;
    font-size: 20px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
    line-height: 28px;
    opacity: 0.3;
  }
  .select-wrap {
    display: flex;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid transparent;
    @include lightThemeOnly {
      border-color: rgba(51, 51, 51, 0.3);
    }
    .select-item {
      min-width: 70px;
      padding: 6px;
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default-5;
      line-height: 28px;
      border-radius: 10px;
      text-align: center;
      user-select: none;
      cursor: pointer;
    }
    .select-active {
      background: rgba(98, 16, 255, 1);
      color: #fff;
    }
  }
}

@include mobile-only {
  .version-select {
    background: rgba(20, 21, 37, 1);

    .label {
      display: none;
    }
    .select-wrap {
      background: transparent;
      border-radius: 0;
      .select-item {
        min-width: 54px;
        padding: 8px 15px;
        font-size: 14px;
        line-height: 20px;
      }
      .select-active {
        position: relative;
        background: transparent;
        &::after {
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0;
          margin-left: -12px;
          width: 24px;
          height: 4px;
          background: linear-gradient(90deg, rgba(144, 23, 255, 1) 0%, rgba(90, 20, 225, 1) 100%);
          border-radius: 2px;
        }
      }
    }
  }
}
